/* =====================Media queries (medium devices) =====================*/
@media screen and (max-width:1024px) {

    .portfolio_container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}

/* =====================Media queries (smalldevices) =====================*/
@media screen and (max-width:600px) {
    .portfolio_container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #DDDBCB;
    --color-bg-variant: #1B9AAA;
    --color-primary: #050505;
    --color-primary-variant: rgba(27, 154, 170, 0.4);
    --color-text-small: #fff;

    --color-white: #fff;
    --color-light: rgba(56, 56, 56, 0.6);
    /* --color-light: rgba(255, 255, 255, 0.8); */

    --transition: all 400ms ease;


    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-ms: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: "Poppins", sans-serif;
    background: #DDDBCB;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(/Portfolio/static/media/bg-texture.3d36c210.png);
    background-size: 20rem;
}

/*=================GENERAL STYLES===========*/

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0px auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    padding: 10px auto;
    font-size: 2.5em;
}

section {
    margin-top: 8em;

}

section>h2,
section>h5 {
    text-align: center;
    color: rgba(56, 56, 56, 0.6);
    color: var(--color-light);
}

section>h2 {
    color: #050505;
    color: var(--color-primary);
    margin-bottom: 3em;
}

.text-light {
    color: rgba(56, 56, 56, 0.6);
    color: var(--color-light);
}

a {
    color: #050505;
    color: var(--color-primary);
    transition: var(---transition);
}

a :hover {
    color: #fff;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #050505;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4em;
    cursor: pointer;
    border: 1px solid #050505;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #DDDBCB;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #050505;
    background: var(--color-primary);
    color: #DDDBCB;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/* =====================Media queries (medium devices) =====================*/
@media screen and (max-width:1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6em;
    }
}


/* =====================Media queries (small devices) =====================*/
@media screen and (max-width:600px) {
    .container {
        width: 90%;
        width: var(--container-width-ms);
    }

    section>h2 {
        margin-top: 2em;
    }
}
