.header_socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 15rem;
}

.header_socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}

/* =====================Media queries (smalldevices) =====================*/
@media screen and (max-width:600px) {
    .header_socials {
        display: none;
    }

}